import * as React from 'react';
import './App.css';
import {fromJS} from 'immutable';

class App extends React.Component<{}, any> {
  constructor() {
    super();
    this.state = {
      model: fromJS([
        {text: 'sample'},
        {text: 'sample1'},
      ])
    };
    document.addEventListener('keypress', this.add.bind(this));
  }

  add() {
    console.log('add');
    this.setState(prevState => ({model: prevState.model.push({text: 'foo bar'})}));
  }

  render() {
    return (
      <div className="App">
        <div className="App-intro">
          {this.state.model.map((_: any, i: any) => <div key={i}>hi</div>)}
        </div>
      </div>
    );
  }
}

export default App;



